.ImageWrapper {
    width: 100%;
    font-size: 0px; /* removes space between sections */
}

.Image {
    width: 100%;
    min-width: 75rem;
}

.MainWrapper {
    min-width: 75rem;
    display: flex;
    justify-content: center;
}

.MainBox {
    min-width: 75rem;
    max-width: 75rem;
}

.PathBox {
    display: flex;
    margin-top: 1.3rem;
}

.PathLink {
    font-size: 1rem;
    color: #EA7119;
    text-decoration: underline;
}

.PathDelimiter {
    margin: 0 1rem;
    color: #707070;
    font-size: 1rem;
}

.ActivePathPage {
    color: #707070;
    font-size: 1rem;
}

.MainTitle {
    margin-top: 2.4rem;
    font-size: 1.5rem;
    font-weight: 700;
    color: #75841C;
}

.MainColumns_Box {
    margin-top: 2.3rem;
    margin-bottom: 6rem;
    /* display: flex; */
    /* justify-content: space-between; */
}

.MainColumn_Box {
    width: 36.6rem;
}

.ColumnImage {
    width: 100%;
}

.TitleMarginTop {
    margin-top: 2.5rem;
}

.ParagraphTitle {
    font-size: 1.25rem;
    font-weight: 700;
    color: #707070;
}

.Paragraph {
    font-size: 1rem;
    color: #707070;
    margin-top: 1rem;
}

.ControlsContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 2.3rem;
    margin-bottom: 1.6rem;
    font-size: 2.2rem;
}

.ControlsMonth {
    margin: 0 1.6rem;
    text-transform: uppercase;
    color: #868686;
    width: 19rem;
    text-align: center;
}

.ControlArrows {
    color: #EA7119;
    cursor: pointer;
    font-size: 3rem;
    user-select: none;
}

.CalendarContainer {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    border: 1px solid #707070;
}

.DayContainer {
    position: relative;
    border: 1px solid #707070;
    height: 12.5rem;
}

.DayCurrentMonth {
    background-color: #FDF8E5;
}

.DayNumber {
    position: absolute;
    right: 0.5rem;
    top: 0.5rem;
    font-size: 1.6rem;
    color: #BDB132;
}

.DayTextsContainer {
    margin: 1rem;
    font-size: 1rem;
    color: #868686;
}

.DayTitle {
    font-weight: 700;
}

.DayText {

}